import { List, Card, Avatar, Divider, Icon, Button } from "antd";
import { Query } from "react-apollo";
import { getProjectOfCurrentUser as GET_PROJECTS_OF_CURRENT_USER } from "../../graphql/queries.gql";
import ListItem from "./projectsList/ListItem";

const addNewProject = [
  {
    title: "Add new project"
  }
];
class ProjectsList extends React.Component {
  render() {
    return (
      <div className="projects-list">
        <List
          grid={{
            gutter: 16,
            xs: 1,
            sm: 2,
            md: 4,
            lg: 4,
            xl: 6,
            xxl: 3
          }}
          dataSource={addNewProject}
          renderItem={item => (
            <List.Item>
              <Card className="add-project-btn">
                <b>
                  <Icon type="plus" /> {item.title}
                </b>
              </Card>
            </List.Item>
          )}
        />
        <Query query={GET_PROJECTS_OF_CURRENT_USER}>
          {({ loading, error, data }) => {
            if (loading) return "Loading...";
            if (error) return `Error! ${error.message}`;
            const {
              projectsAsAdminOfCurrentUser,
              projectsAsMemberOfCurrentUser
            } = data;
            return (
              <div>
                {projectsAsAdminOfCurrentUser.length > 0 && (
                  <React.Fragment>
                    <Divider orientation="left">
                      <Icon type="solution" style={{ marginRight: 8 }} />As
                      Admin
                    </Divider>
                    <List
                      grid={{
                        gutter: 16,
                        xs: 1,
                        sm: 2,
                        md: 4,
                        lg: 4,
                        xl: 6,
                        xxl: 3
                      }}
                      dataSource={projectsAsAdminOfCurrentUser}
                      renderItem={project => <ListItem project={project} />}
                    />

                    {projectsAsMemberOfCurrentUser.length > 0 && (
                      <React.Fragment>
                        <Divider orientation="left">
                          <Icon type="user" style={{ marginRight: 8 }} />
                          As Member
                        </Divider>
                        <List
                          grid={{
                            gutter: 16,
                            xs: 1,
                            sm: 2,
                            md: 4,
                            lg: 4,
                            xl: 6,
                            xxl: 3
                          }}
                          dataSource={projectsAsMemberOfCurrentUser}
                          renderItem={project => project => (
                            <ListItem project={project} />
                          )}
                        />
                      </React.Fragment>
                    )}
                  </React.Fragment>
                )}
              </div>
            );
          }}
        </Query>
      </div>
    );
  }
}

export default ProjectsList;



